/* Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Smooth Scroll */
html {
  scroll-behavior: smooth;
}

/* Base Styles */
body {
  height: 100%;
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
  color: white;
  overflow-x: hidden;
}

/* Container for all sections */
.home-container {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  gap: 2rem;
  padding: 2rem 1rem;
}

/* Section layout */
section {
  padding: 4rem 2rem;
  min-height: 100vh;
}

/* Hero Section */
.hero {
  text-align: center;
  padding: 3rem 1rem;
}

.hero h1 {
  font-size: 2.5rem;
}

.hero p {
  font-size: 1.2rem;
  margin: 1rem 0;
}

.hero-button {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.75rem 1.5rem;
  background-color: #ffffff20;
  color: white;
  text-decoration: none;
  border: 1px solid white;
  border-radius: 8px;
  transition: background-color 0.3s;
}

.hero-button:hover {
  background-color: #ffffff40;
}

/* About, Projects, Certifications, Contact */
.about-section,
.project-section,
.certification-section,
.contact-section {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 1.5rem;
  border-radius: 10px;
  backdrop-filter: blur(3px);
}

/* Reusable link-style button */
.link-button {
  display: inline-block;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  background-color: #ffffff20;
  color: white;
  text-decoration: none;
  border: 1px solid white;
  border-radius: 6px;
  transition: 0.3s;
}

.link-button:hover {
  background-color: #ffffff40;
}

/* Project Cards */
.project-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.card {
  flex: 1 1 45%;
  background-color: rgba(255, 255, 255, 0.15);
  padding: 1rem;
  border-radius: 10px;
  backdrop-filter: blur(5px);
}

/* Certifications List */
.certification-section ul {
  list-style: disc;
  padding-left: 1.5rem;
}

.certification-section li {
  margin-bottom: 0.75rem;
}

/* Contact Section */
.contact-section p {
  margin-bottom: 0.75rem;
}

/* Responsive Styles */
@media (max-width: 768px) {
  .hero h1 {
    font-size: 2rem;
  }

  .project-cards {
    flex-direction: column;
  }

  .card {
    flex: 1 1 100%;
  }
}
